import { Component, OnInit } from '@angular/core';
import { UserService } from '../services/user.service';
import { Router, ActivatedRoute } from '@angular/router';
import { IUser } from '../models/users';
import { FormGroup, FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {
  public user: IUser;
  constructor(
    private userService: UserService,
    private router: Router,
    public activatedRoute: ActivatedRoute
  ) { }

  profileForm: FormGroup;
  firstName: FormControl = new FormControl("", [Validators.required]);
  lastName: FormControl = new FormControl("", [Validators.required]);
  email: FormControl = new FormControl("", [Validators.required]);
  phone: FormControl = new FormControl("", [Validators.required]);
  mobile: FormControl = new FormControl("", [Validators.required]);

  ngOnInit() {
    this.profileForm = new FormGroup({
      firstName: this.firstName,
      lastName: this.lastName,
      email: this.email,
      phone: this.phone,
      mobile: this.mobile
    });
    this.userService.getprofile(
      this.activatedRoute.snapshot.params["id"])
      .subscribe(data => {
        this.user = data;
        this.firstName.setValue(this.user.firstName);
        this.lastName.setValue(this.user.lastName);
        this.email.setValue(this.user.email);
        this.phone.setValue(this.user.phone);
        this.mobile.setValue(this.user.mobile);
      });
  }
  save(){
    this.userService.editUser(this.profileForm.value)
    // .subscribe(data => {
    // if (data) {
    //   this.router.navigate(["adminProducts"]);
    // }
  // });
}
  

}
