import { Component, OnInit } from '@angular/core';
import { ProductService } from '../services/product.service';
import { Router } from '@angular/router';
import { FormGroup, FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-add-product',
  templateUrl: './add-product.component.html',
  styleUrls: ['./add-product.component.css']
})
export class AddProductComponent implements OnInit {

  constructor(
    private productService: ProductService,
    private router: Router) { }
  addProductForm: FormGroup;

  name: FormControl = new FormControl("", [Validators.required]);
  title: FormControl = new FormControl("", [Validators.required]);
  subTitle: FormControl = new FormControl("", [Validators.required]);
  price: FormControl = new FormControl("", [Validators.required]);
  description: FormControl = new FormControl("", [Validators.required]);
  imageOnlineURL: FormControl = new FormControl("", [Validators.required]);

  ngOnInit() {
    this.addProductForm = new FormGroup({
      name: this.name,
      title: this.title,
      subTitle: this.subTitle,
      price: this.price,
      description: this.description,
      imageOnlineURL: this.imageOnlineURL
    });
  }
  save() {
    this.productService.addProduct(this.addProductForm.value).subscribe(data => {
      if (data) {
        this.router.navigate(["adminProducts"]);
      }
    });
  }

}
