import { Component, OnInit } from '@angular/core';
import { ProductService } from '../services/product.service';
import { Router, ActivatedRoute } from '@angular/router';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { IProduct } from '../models/product';

@Component({
  selector: 'app-edit-product',
  templateUrl: './edit-product.component.html',
  styleUrls: ['./edit-product.component.css']
})
export class EditProductComponent implements OnInit {
  public product: IProduct;
  // product;
  constructor(
    private productService: ProductService,
    private router: Router,
    public activatedRoute: ActivatedRoute) { }

  editProductForm: FormGroup;
  name: FormControl = new FormControl("", [Validators.required]);
  title: FormControl = new FormControl("", [Validators.required]);
  subTitle: FormControl = new FormControl("", [Validators.required]);
  price: FormControl = new FormControl("", [Validators.required]);
  description: FormControl = new FormControl("", [Validators.required]);
  imageOnlineURL: FormControl = new FormControl("", [Validators.required]);

  ngOnInit() {
    this.editProductForm = new FormGroup({
      name: this.name,
      title: this.title,
      subTitle: this.subTitle,
      price: this.price,
      description: this.description,
      imageOnlineURL: this.imageOnlineURL
    });
    this.productService.getProduct(
      this.activatedRoute.snapshot.params["id"])
      .subscribe(data => {
        this.product = data;
        this.name.setValue(this.product.name);
        this.title.setValue(this.product.title);
        this.subTitle.setValue(this.product.subTitle);
        this.price.setValue(this.product.price);
        this.description.setValue(this.product.description);
        this.imageOnlineURL.setValue(this.product.image);
      });
  }
  save() {
    this.productService.editProduct(this.editProductForm.value).subscribe(data => {
      if (data) {
        this.router.navigate(["adminProducts"]);
      }
    });
  }

}
