import { Injectable, InjectableProvider } from "@angular/core";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Observable } from 'rxjs';
import { IProduct } from '../models/product';

const url: string = "http://oday9.azurewebsites.net";
@Injectable({
    providedIn: "root"
})
export class ProductService {
    countTotal: number = 0;
    reqHeaderToken;
    constructor(public http: HttpClient) {
        let token = localStorage.getItem("token");
        if (token) {
            this.getTotal().subscribe(data => this.countTotal = +data);
        }
        this.reqHeaderToken = new HttpHeaders({
            Authorization: "bearer " + token
        });
    }

    getProducts(): Observable<IProduct[]> {
        return this.http.get<IProduct[]>(url + "/api/Products");
    }

    getProduct(id): Observable<IProduct> {
        return this.http.get<IProduct>(`${url}/api/Products/${id}`);
    }

    addProduct(product: IProduct) {
        return this.http.post(`${url}/api/Products`, product);
    }

    editProduct(product: IProduct) {
        return this.http.put(`${url}/api/Products`, product);
    }

    deleteProduct(id) {
        return this.http.delete(`${url}/api/Products/${id}`);
    }

    addToCart(productId) {
        return this.http.post(`${url}/api/Carts`, { productId: productId },
            { headers: this.reqHeaderToken });
    }

    getCart() {
        return this.http.get(`${url}/api/Carts`,
            { headers: this.reqHeaderToken });
    }
    getTotal() {
        return this.http.get(`${url}/api/Carts/GetTotal`,
            { headers: this.reqHeaderToken });

    }



}